.main-slider {
  &__slide {
    height: 544px;
    background-image: url(../img/bg-main-slider-moblie.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 100%;
    position: relative;
  }

  &__text-content {
    margin-top: 31px;
    width: 80.4%;
  }

  &__title {
    font-family: Gothic, serif;
    font-weight: 300;
    font-size: 2.4rem;
    line-height: 125%;
