    margin-bottom: 16px;
  }

  &__subtitle {
    font-family: Arial, sans-serif;
    font-size: 1.6rem;
    line-height: 125%;
    text-transform: uppercase;
    color: $gray-99;
    margin-bottom: 48px;
  }

  &__img-wrap {
    position: absolute;
    bottom: 88px;
    left: 50%;
    transform: translateX(-50%) rotate(3.27deg);
    width: 220px;
    height: 124px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__img {
    max-width: 100%;
    max-height: 100%;
    width: 100%;
    height: auto;
  }

  &__additional-img-wrap {
    display: none;
  }

  .slick-dots {
    list-style: none;
    padding: 0;
    display: flex;
    margin: 0;
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);

    li {
      font-size: 0;
      width: 12px;
      height: 12px;
      border: 1px solid $blue-61;
      margin: 0 4px;
      border-radius: 50%;

      button {
        padding: 0;
        border: 1px solid transparent;
        font-size: 0;
        width: 100%;
        height: 100%;
        border-radius: 50%;
      }

      &.slick-active {
        button {
          border-color: $white;
          background: $blue-61;
        }
      }
    }
  }
}

