    padding: 0;
  }

  .breadcrumb-item,
  .breadcrumb-item a {
    font-family: Arial, sans-serif;
    font-size: 1.3rem;
    line-height: 123%;
    text-align: left;
    color: $gray-сc;
  }
}

.product {
  padding: 0;
  font-family: Arial, sans-serif;

  &__card {
    padding: 30px 0;
    max-width: 1328px;
    margin: 0 auto;

    &.sale {
      position: relative;

      &::after {
        content: "sale";
        display: flex;
        justify-content: center;
        align-items: center;
        width: 44px;
        height: 44px;
        background: $black;
        border-radius: 50%;
        font-size: 1.1rem;
        line-height: 1;
        text-transform: uppercase;
        color: $white;
        position: absolute;
        left: 32px;
        top: 10px;
      }
    }
  }

  &__view {
    padding: 0 16px;
    flex: 0 0 100%;
  }

  &__showing-slider {
    display: none;
  }

  &__slide-wrap {
    width: 100%;
    height: 312px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__slide-img {
    max-width: 100%;
    height: auto;
    max-height: 100%;
  }

  .slick-dots {
    list-style: none;
    padding: 0;
    display: flex;
    margin: 0;
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);

    li {
      font-size: 0;
      width: 12px;
      height: 12px;
      border: 1px solid $blue-61;
      margin: 0 4px;
      border-radius: 50%;

      button {
        padding: 0;
        border: 1px solid transparent;
        font-size: 0;
        width: 100%;
        height: 100%;
        border-radius: 50%;
      }

      &.slick-active {
        button {
          border-color: $white;
          background: $blue-61;
        }
      }
    }
  }

  &__social-d {
    display: none;
  }

  &__title {
    font-size: 1.6rem;
    line-height: 125%;
    color: $gray-сc;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 24px;
  }

  &__img-lists {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 0;
  }

  &__img-list {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 23px;
    height: 24px;
    margin: 0 8px;
  }

  &__img-link {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__svg {
    width: 100%;
    height: auto;
    max-height: 100%;
  }


  &__description {
    padding: 24px 0 0;
  }

  &__header {
    padding: 0 16px;
  }

  &__box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 0 16px;
    border-bottom: 1px solid $white-f4;
  }

  &__header-box {
    flex: 1 0 64%;
  }

  &__header-title {
    font-family: Gothic, serif;
    font-weight: 300;
    font-size: 2.4rem;
    line-height: 125%;
    color: $black;
    margin-bottom: 4px;
  }

  &__header-subtitle {
    font-size: 1.3rem;
    line-height: 1.1;
    text-transform: uppercase;
    color: $gray-сc;
    margin-bottom: 0;
  }

  &__rating {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__rating-lists {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 0;
  }

  &__rating-list {
    width: 12px;
    height: 12px;
    display: flex;
    justify-content: center;
    align-items: center;

    svg {
      width: 100%;
      height: 100%;
      fill: $gray-сc;
    }

    &.gold {
      svg {
        fill: $gold;
      }
    }
  }

  &__count {
    font-size: 1.6rem;
    line-height: 125%;
    align-items: center;
    text-transform: uppercase;
    color: $black;
    margin-left: 5px;

    &:not(:empty) {
      &::before {
        content: '(';
      }

      &::after {
        content: ')';
      }
    }
  }

  &__pay {
    padding: 16px;
  }

  &__price {
    font-family: Gothic, serif;
    font-weight: 300;
    line-height: 125%;
    text-align: left;
    color: $black;
    margin-bottom: 24px;

    &-actual {
      font-size: 2.4rem;
      margin-right: 20px;
    }

    &-old {
      font-size: 1.6rem;
      text-decoration: line-through;
    }
  }

  &__quantity {
    margin-bottom: 16px;
  }

  &__btn-card {
    width: 100%;
    padding: 18px;
    font-size: 1.6rem;
    margin-bottom: 16px;
  }

  &__btn-buy {
    width: 100%;
    padding: 18px;
    font-size: 1.6rem;
  }

  &__delivery {
    border-top: 1px solid $white-f4;
    padding: 16px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;

    &-img-wrap {
      width: 31px;
      flex: 0 0 31px;
      height: 24px;
      margin-right: 16px;
    }

    &-img {
      width: 100%;
      height: 100%;
    }

    &-text {
      font-size: 1.6rem;
      line-height: 125%;
      color: $black;
    }

    &-link {
      display: block;
      color: $blue-61;
    }
  }


  &__accordion {
    padding: 0 16px;
    margin-bottom: 33px;

    .card {
      border-left: none;
      border-right: none;

      &-header {
        padding: 0;
        margin: 0;
        border-bottom: none;
      }

      &-body {
        -webkit-box-flex: 1;
        -ms-flex: 1 1 auto;
        flex: 1 1 auto;
        min-height: 1px;
        padding: 0 12px 24px 0;
        font-size: 1.6rem;
        line-height: 125%;
        color: $black;
      }
    }

    &-btn {
      border: none;
      font-family: Arial, sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 1.6rem;
      line-height: 125%;
      text-transform: uppercase;
      color: $black;
      background: $white;
      padding: 24px 0;
      display: flex;
      justify-content: flex-start;
      align-items: center;

      &::before {
        content: "";
        display: inline-block;
        width: 6px;
        height: 8px;
        background: url(../img/dropdown.svg) no-repeat;
        transform: rotate(-90deg);
        transition: all 0.3s ease;
        margin-right: 4px;
      }

      &.collapsed::before {
        transform: rotate(90deg);
      }

      &:hover {
        color: $black;
      }
    }

    &-title {
      color: $gray-сc;
      text-transform: uppercase;
      margin-bottom: 8px;
    }

    &-text {
      color: $black;
      margin-bottom: 16px;
    }

    &-img-wrap {
      max-width: 100%;
      width: auto;
      height: 51px;
      display: inline-block;
    }

    &-img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
}

.quantity {
  display: flex;
  justify-content: flex-start;
  align-items: center;

  &__symbol {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 56px;
    height: 56px;
    background: $white-f4;
    border: 1px solid $gray-сc;
    color: $gray-сc;
    font-size: 1.6rem;
    line-height: 1;
    transition: all .3s ease;

    &:hover {
      background: fade-out($color: $white-f4, $amount: 0.5);
      color: $black;
      text-decoration: none;
    }

    &:active,
    &:focus {
      text-decoration: none;
      color: $black;
    }
  }

  &__input {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 56px;
    height: 56px;
    border-top: 1px solid $white-f4;
    border-bottom: 1px solid $white-f4;
    border-left: none;
    border-right: none;
    padding: 10px;
    text-align: center;
    font-size: 1.6rem;
    color: $black;
  }

}


.slogan {
  padding: 50px 16px 63px;
  background: $white;
  position: relative;

  &__wrap {
    border: 1px solid $white-f4;
    padding-top: 56px;
  }

  &__img-wrap {
    width: 76px;
    height: 54px;
    margin: 0 auto 16px;

    svg {
      margin: 0 auto;
      height: 100%;
      width: auto;
      max-width: 100%;
    }
  }

  &__title {
    font-family: Gothic, serif;
    font-weight: 300;
    font-size: 1.8rem;
    line-height: 133%;
    text-align: center;
    color: $black;
    width: 56%;
    // min-height: 46px;
    margin: 0 auto;
    background: $white;
    position: absolute;
    left: 50%;
    top: 15px;
    transform: translateX(-50%);
  }

  &__subtitle {
    font-size: 16px;
    line-height: 125%;
    text-align: center;
    text-transform: uppercase;
    color: $black;
    margin-bottom: 16px;
  }

  &__text {
    font-size: 16px;
    line-height: 125%;
    text-align: center;
    color: $black;
  }

  &__block {
    margin: 0 auto 32px;
    width: 85.9%;
  }

  &__devider {
    width: 71px;
    height: 48px;
    margin: 0 auto 32px;

    * {
      width: 100%;
      height: 100%;
    }
  }
}